<template>
  <div class="service-login-wrap">
    <div v-title>噢啦合作服务站</div>
    <!--页面内容-->
    <div class="service-login">
      <!--头部图标-->
      <div class="login-logo">
        <img class="logo-pic" src="../../assects/images/oola.png" alt="噢啦">
        <p class="logo-title">噢啦服务站登录</p>
      </div>

      <!--填写账号和密码-->
      <div class="login-form">
        <!--账号-->
        <div class="form-user">
          <img
            v-if="!checkUser"
            src="../../assects/images/partner-no.png"
            class="input-icon">
          <img
            v-if="checkUser"
            src="../../assects/images/partner-yes.png"
            class="input-icon">
          <input
            v-model.trim="form.userName"
            type="text"
            placeholder="请输入手机号"
            class="data-input">
          <button
            :disabled="!canGetCode"
            @click="getCode"
            :class="{canNoGetCode: !canGetCode}"
            class="code-btn">{{ getCodeBtnTitle }}
          </button>
        </div>
        <!--密码-->
        <div class="form-pwd">
          <img
            v-if="!checkPwd"
            src="../../assects/images/key-no.png"
            class="input-icon">
          <img
            v-if="checkPwd"
            src="../../assects/images/key-yes.png"
            class="input-icon">
          <input
            v-model.trim="form.password"
            type="password"
            placeholder="请输入验证码"
            class="data-input">
        </div>
        <!--登录按钮-->
        <div class="form-btn">
          <input
            @click="submitForm"
            type="button"
            value="登录"
            class="btn-input"
            :disabled="!checkForm"
            :class="{successInput: checkForm}">
        </div>
      </div>

      <!--跳转到合作商登录页面-->
      <router-link
        tag="div"
        class="router-go"
        :to="{path: '/partner-login'}">合作商登录
      </router-link>

      <!--底部栏-->
      <div class="login-footer">
        <p>商务合作</p>
        <p>020-38770773</p>
        <p>bd@oolagongyi.com</p>
      </div>
    </div>
  </div>
</template>
<script>
  import {Toast} from 'mint-ui'
  import {serviceLoginApi, getLoginCodeApi} from '@/API'

  export default {
    data() {
      return {
        getCodeBtnTitle: '获取验证码',
        canGetCode: true, // 是否可获取验证码
        // 表单数据
        form: {
          userName: '',
          password: ''
        }
      }
    },
    computed: {
      checkUser: function () {
        let self = this
        let reg=/^[1][3,4,5,7,8][0-9]{9}$/
        if (!reg.test(self.form.userName)) {
          return false
        } else {
          return true
        }
      },
      checkPwd: function () {
        let self = this
        if (self.form.password !== '') {
          return true
        }
        return false
      },
      checkForm: function () {
        let self = this
        if (self.form.userName !== '' && self.form.password !== '') {
          return true
        }
        return false
      }
    },
    methods: {
      submitForm: function () {
        let self = this
        if (!self.checkUser) {
          Toast('请填写正确的手机号码')
          return
        }
        if (self.checkForm && self.checkUser) {
          serviceLoginApi({
            telephone: self.form.userName,
            verifyCode: self.form.password
          })
            .then(res => {
              if (res.status === 'true') {
                sessionStorage.token = res.info.token
                Toast('登录成功');
                setTimeout(() => {
                  self.$router.replace({
                    path: '/service-home'
                  })
                }, 1000)
              } else {
                Toast(res.msg)
              }
            })
            .catch(() => {
              Toast('登录失败')
            })
        }
      },
      getCode: function () {
        let self = this
        if (!self.checkUser) {
          Toast('请填写正确的手机号码')
          return
        }
        getLoginCodeApi({
          telephone: self.form.userName,
          scenario: 'channelServicePointLogin'
        })
          .then(res => {
            if (res.status === 'true') {
              Toast('短信已发送')
              let count = 60
              let timer = setInterval(() => {
                count--
                self.canGetCode = false
                self.getCodeBtnTitle = count + 's'
                if (count === -1) {
                  clearInterval(timer)
                  self.canGetCode = true
                  self.getCodeBtnTitle = '获取验证码'
                }
              }, 1000)
            } else {
              Toast(res.msg)
            }
          })
          .catch(() => {
            Toast('获取失败')
          })
      }
    }
  }
</script>
<style lang="less" scoped>
  .service-login-wrap {
    background-color: whitesmoke;
  }

  .service-login {
    min-height: 100vh;
    background: url("../../assects/images/login-bg.png") no-repeat;
    background-size: 100% 100%;
    /*logo图标*/
    .login-logo {
      padding-top: 44px;
      text-align: center;
      .logo-pic {
        width: 95px;
      }
      .logo-title {
        font-family: PingFangSC-Semibold;
        font-size: 19px;
        color: #191D21;
      }
    }
    /*表单样式*/
    .login-form {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 58px;
      .data-input {
        width: 80vw;
        padding-left: 25px;
        border: 0;
        border-bottom: 1px solid #E1E1E6;
        background-color: rgba(0, 0, 0, 0);
        font-size: 16px;
        line-height: 2;
      }
      .input-icon {
        position: absolute;
        width: 22px;
        height: 22px;
        left: 0;
        top: 8px;
      }
      .form-user {
        display: inline-block;
        position: relative;
        .code-btn {
          display: block;
          width: 90px;
          position: absolute;
          right: 0;
          bottom: 8px;
          border: 0;
          border-left: 1px solid #CBD3DB;
          color: #FFB717;
          background-color: rgba(0, 0, 0, 0);
          text-align: center;
          line-height: 2;
        }
        .canNoGetCode {
          color: #CDCED3;
        }
      }
      .form-pwd {
        margin-top: 20px;
        display: inline-block;
        position: relative;
      }
      .form-btn {
        .btn-input {
          width: 80vw;
          height: 45px;
          margin-top: 45px;
          background: #CDCED3;
          border-radius: 8px;
          border: 0;
          font-size: 17px;
          color: #FFFFFF;
        }
        .successInput {
          background-color: #FFB717;
        }
      }
    }
    /*登录切换跳转*/
    .router-go {
      margin-top: 15px;
      text-align: center;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #FFB717;
    }
    /*底部栏*/
    .login-footer {
      margin-top: 8vh;
      text-align: center;
      font-size: 13px;
      color: #9D9DA5;
      line-height: 20px;
    }
  }
</style>



